.App {
   background-size: cover;
   height: 100vh; /* Adjust the height as needed */
   overflow: auto; /* Add scrollbars if the content overflows */
   /* Other styles for your app */
   background-repeat: no-repeat; 
   background: linear-gradient(
      to bottom,
      rgba(61, 11, 51, 0.8),
      rgba(48, 5, 48, 0.8),
      rgba(34, 0, 44, 0.8),
      rgba(17, 0, 22, 0.8),
      rgba(0, 0, 0, 0.8)
    );
} 
